import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import FavoriteIcon from '@material-ui/icons/Favorite';
import CommentIcon from '@material-ui/icons/Comment'
import Share from '@material-ui/icons/Share'
import Typography from '@material-ui/core/Typography';
import PostOptions from './postOptions'
import Comment from '../Comment'
import CommentWriter from '../CommentWriter'

import { connect } from 'react-redux'
import { likePost, unlikePost } from '../../redux/actions/likeActions'
import { getComments } from '../../redux/actions/commentActions'

const styles = {
  card: {
    width: "600px",
    "position": "relative",
    "left": "50%",
    "top": "50%",
    "transform": "translate(-50%, 0%)",
    "margin-top": "25px",
    "margin-bottom": "25px"
  },
  avatar: {
      "background-color": "rgb(0, 188, 212)",
  },
  action: {
      display: "flex",
      "justify-content": "space-around",
  },
  liked: {
      "color": "rgb(0, 188, 212)"
  },
  number: {
    position: "relative",
	right: "6px"
  },
  comments: {
    "border-top": "1px solid #cccccc",
    "max-height": "500px",
    "overflow-y": "auto",
    "overflow-x": "hidden"
  }
};

class Post extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            commentLoading: false
        }
    }
    render() {
        const { classes, post, likeStatus, likePost, unlikePost, comments, commentCount } = this.props;
        dayjs.extend(relativeTime);
        return(
            <Card elevation = {4} key = {post._id} className={classes.card}>
                <CardHeader
                    avatar={
                        <Avatar className={classes.avatar}>
                            {post.author[0].toUpperCase()}
                        </Avatar>
                    }
                    title = {post.author}
                    subheader= {dayjs(post.createdAt).fromNow()}
					action = {
						(post.authorId === JSON.parse(localStorage.user)._id)
						? (
							<PostOptions/>
						) : (
							""
						)
					}
                />
                <CardContent>
                <Typography variant="body2" component="p">
                    {post.content}
                </Typography>
                </CardContent>
                <CardActions className = {classes.action}>
                    <div>
                        <IconButton
                            title = "Like" 
                            onClick = {(likeStatus) ? () => unlikePost(post._id)
                                                        : () => likePost(post._id)}
                        >
                            <FavoriteIcon className = {(likeStatus) ? classes.liked : ""}/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {post.likeCount}
                        </Typography>
                    </div>
                    <div>
                        <IconButton title = "Comment" onClick = {
                            () => this.props.getComments(post._id)
                        }>
                            <CommentIcon/>
                        </IconButton>
                        <Typography variant = "caption" className = {classes.number}>
                            {commentCount}
                        </Typography>
                    </div>
                    <IconButton title = "Share">
                        <Share/>
                    </IconButton>
                </CardActions>
                    {comments ? (
                        <div>
                            <div className = {classes.comments}>
                                {comments.map((comment) => 
                                        <Comment key = {comment._id} comment = {comment}/>
                                )}
                            </div>
                            <CommentWriter author = {post.author} postId = {post._id}/>
                        </div>
                        ) : (
                            ""
                        )}
            </Card>
        )
    }
}

const mapActionsToProps = {
    likePost,
    unlikePost,
	getComments
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(Post))