import React from 'react'
import { withStyles } from '@material-ui/core/styles';
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Typography from '@material-ui/core/Typography';

import { connect } from 'react-redux'
import { deleteComment } from '../../redux/actions/commentActions'

const styles = {
  card: {
    "position": "relative",
    "left": "50%",
    "top": "50%",
    "transform": "translate(-50%, 0%)",
	  "box-shadow": "none"
  },
  avatar: {
    "background-color": "rgb(0, 188, 212)",
	  width: "30px",
	  height: "30px",
	  "font-size": "0.95rem"
  },
  moreVertIcon: {
	  "font-size": "1.25rem"
  },
  commentPadding: {
	  padding: "4px 10px 4px 10px"
  } 
};

class Comment extends React.Component{
	render() {
		const { classes } = this.props;
		const { author, content, createdAt, _id } = this.props.comment
		return(
            <Card key = {_id} className={classes.card}>
                <CardHeader
                    avatar={
                    <Avatar className={classes.avatar}>
                        {author[0].toUpperCase()}
                    </Avatar>
                    }
                    title = {author}
          					subheader= {dayjs(createdAt).fromNow()}
          					action = {
          						<IconButton>
          							<MoreVertIcon className = {classes.moreVertIcon}/>
          						</IconButton>
          					}
					          className = {classes.commentPadding}
                />
                <CardContent className = {classes.commentPadding}>
        					<Typography variant="body2" component="p">
        						{content}
        					</Typography>
                </CardContent>
          </Card>	
		)
	}
}

const mapActionsToProps = {
  deleteComment
}

export default connect(
  null,
  mapActionsToProps
)(withStyles(styles)(Comment));