import React from 'react';
import { MenuList, MenuItem, IconButton, Popper, Paper } from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert'

class PostOptions extends React.Component {
   constructor() {
     super()
     this.state = {
       anchorEl: null
     }
   }
   toggleMenu = (e) => {
    if(!this.state.anchorEl) {
      this.setState({anchorEl: e.currentTarget})
    }
    else {
      this.setState({anchorEl: null})
    }
   }
    render() {
        return (
            <div>
              <IconButton onClick={this.toggleMenu}>
                <MoreVertIcon/>
              </IconButton>
              <Popper open = {Boolean(this.state.anchorEl)} anchorEl ={this.state.anchorEl}>
                <Paper elevation = {2}>
                  <MenuList
                    id="simple-menu"
                    autoFocusItem = {Boolean(this.state.anchorEl)}
                  >
                      <MenuItem>Edit</MenuItem>
                      <MenuItem>Delete</MenuItem>
                  </MenuList>
                </Paper>
              </Popper>
            </div>
        );
    }
}

export default PostOptions;